<template>
   <div class="scan-page" :class="{'cash-card': type == 'cash_card'}" v-scroll="scrollFn">
    <Header class="scan-header" :class="{'header-bg': isFixed}">
      <img class="back-btn" v-if="!$route.query.browerFirstIn" slot="left" :src="$globalIcon.whiteBackIcon" alt />
      <div class="header-text" slot="title">{{title}}</div>
    </Header>
    <div class="scan-warp">
      <a href="javascript:;" v-if="type === 'aggre_pay'" class="save-btn" :class="{'active': showRefresh}" @click="toSave">保存图片到本地</a>
      <div class="scan-body" :class="{'active': type === 'aggre_pay'}" id="scanPayCanvas">
        <div class="scan-code">
          <div class="scan-title">
            <img src="https://cdn09.ehaier.com/shunguang/H5/www/img/sgmobile/order/casher/yuan.png" alt="">订单支付码
          </div>
          <div class="scan-content">
            <div class="scan-refresh" v-if="showRefresh">
              如果付款失败，请 <a href="javascript:;" @click="refresh">点击刷新</a>
            </div>
            <template v-if="type === 'aggre_pay'">
              <p>使用支付宝或微信扫描二维码支付</p>
              <img :src="imgUrl" alt="">
            </template>
            <template v-if="type === 'cash_card'">
              <div class="cash-price">
                <h4>应付金额</h4>
                <p v-if="orderInfo.orderAmount"><small>¥</small>{{orderInfo.orderAmount | formatPriceInt}}<small>{{orderInfo.orderAmount | formatPriceFloat}}</small></p>
              </div>
              <img :src="imgUrl" alt="">
              <p>需前往收银台出示此二维码支付</p>
            </template>
          </div>
        </div>
        <div class="order-info">
          <div>
            <span>订单号</span>
            <small>{{orderSn}}</small>
          </div>
          <div>
            <span>下单时间</span>
            <small>{{orderInfo.orderTime}}</small>
          </div>
          <div v-if="type === 'aggre_pay'">
            <span>应付金额</span>
            <strong>¥{{orderInfo.orderAmount}}</strong>
          </div>
        </div>
      </div>
    </div>
    <van-overlay :show="saving">
      <div class="wrapper" @click.stop>
          <van-loading />
      </div>
    </van-overlay>
  </div>
</template>

<script>
import Header from '@/components/header'
import URL from '@/http/url'
import html2canvas from 'html2canvas'
import QRCode from 'qrcode'
import config from '@/config'

export default {
  name: 'scanpay',
  components: {
    Header
  },
  data() {
    return {
      isFixed: false,
      orderSn: '',
      orderInfo: {},
      imgUrl: '',
      orderSource: '', // 订单来源标识
      timer: null, // 查询计时器
      count: 0, // 计数
      showRefresh: false, // 是否刷新
      saving: false,
      title: '',
      type: '',
    }
  },
  directives: {
    scroll: {
      inserted (el, binding) {
        el.addEventListener('scroll', (ev) => {
          binding.value(el.scrollTop)
        })
      }
    }
  },
  mounted () {
    clearInterval(this.timer)
    this.type = this.$route.query.type
    this.orderSource = this.$route.query.orderSource || ''
    this.orderSn = this.$route.params.orderSn

    this.initFetch()
    this.checkPayResult()
  },
  destroyed() {
    clearInterval(this.timer)
  },
  methods: {
    // 初始化分类请求
    initFetch() {
      if (this.type === 'cash_card') {
        this.title = '现金刷卡支付'
        this.createCashPayQRcode()
      } else if (this.type === 'aggre_pay') {
        this.title = '线上聚合扫码支付'
        this.createPayQRcode()
      }
    },
    // 现金刷卡支付
    createCashPayQRcode() {
      this.$http.post(URL.getCashPayQRcode + '?orderSn=' + this.orderSn).then(res => {
        let result = res.data;
        console.log(result)
        QRCode.toDataURL(result.data.concatCiphertext)
          .then(url => {
            console.log(url)
            this.imgUrl = url
          })
      })
    },
    // 聚合扫码支付
    createPayQRcode() {
      QRCode.toDataURL(`${config.HOST}/sgmobile/scanmiddle/${this.orderSn}?t=${Date.now()}`)
        .then(url => {
          console.log(url)
          this.imgUrl = url
        })
        .catch(err => {
          console.error(err)
        })
    },
    checkPayResult() { // 检测是否支付成功
      let run = () => {
        this.count++;
        if (this.count >= 200) {
          clearInterval(this.timer)
          this.showRefresh = true
          return;
        }
        this.$http.get(URL.getSimpleOrderInfo, {
          params: {
            orderSn: this.orderSn
          }
        }).then(({ data }) => {
          let result = data;
          this.orderInfo = result.data
          if (this.orderInfo.paymentStatus === 101) {
            clearInterval(this.timer)
            this.$router.replace('/payState?data={"is_success":"T","order_id": "' + this.orderInfo.orderId + '", "orderSource": "' + this.orderSource + '"}')
          }
        })
      }
      this.timer = setInterval(run, 3000)
      run()
    },
    refresh() { // 刷新二维码
      this.showRefresh = false
      this.timer = null
      this.count = 0
      this.checkPayResult()
    },
    scrollFn(scrollTop) {
      this.isFixed = scrollTop > 10
    },
    toSave() {
      // this.$util.imgToBase64(this.imgUrl).then(base64 => {
      //   if (base64) {
      //     console.log(base64)
      if (this.saving) {
        return
      }
      this.saving = true
      html2canvas(document.querySelector('#scanPayCanvas')).then(canvas => {
        // console.log(canvas.toDataURL())
        let base64 = canvas.toDataURL()
        if (this.$util.isHaierApp()) {
          this.$util.uplusSaveImage(base64).then(res => {
            this.saving = false
            if (res) {
              this.$toast('保存成功')
            } else {
              this.$toast('保存失败')
            }
          }).catch(e => {
            this.saving = false
            this.$toast('保存失败')
          })
        } else {
          const a = document.createElement('a') // 创建一个a节点插入的document
          const event = new MouseEvent('click') // 模拟鼠标click点击事件
          a.download = Date.now() // 设置a节点的download属性值
          a.href = base64 // 将图片的src赋值给a节点的href
          a.dispatchEvent(event)
          this.saving = false
        }
      });
    }
    // })
    // }
  }
}
</script>

